* {
  margin: 0;
  padding: 0;
}
body {
  background-color: #333333;
  overflow: hidden;
}
#container-loading {
  position: fixed;
  top: 0;right: 0;bottom: 0;left: 0;
  width: 100%;
  height: 100%;
  background-color: #333;
  z-index: 2;
}
.loading {
  position: absolute;
  top: 48%;
  left: 50%;
  margin-left: -200px;
  width: 400px;
  height: 14px;
  border: 1px solid #186060;
  border-radius: 7px;
  box-sizing: border-box;
}
#per {
  width: 0;
  height: 100%;
  line-height: 13px;
  color: #00ffff;
  background: #00ffff;
  font-size: 14px;
  border-radius: 7px;
}
#container-canvas {
  visibility: hidden;
}

.btn-controls {
  position: fixed;
  right: 4%;
  bottom: 5%;
  width: 42px;
  z-index: 1;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .8);
  border-radius: 5px;
}
.btn-audio, .btn-run, .btn-dimensions {
  width: 42px;
  height: 42px;
}
.btn-audio{
  background: url('../icon/voice-off.png') no-repeat center;
  background-size: 70%;
}
.play {
  background: url('../icon/voice-on.png') no-repeat center;
  background-size: 70%;
}
.btn-run {
  background: url('../icon/play-off.png') no-repeat center;
  background-size: 70%;
}
.run {
  background: url('../icon/play-on.png') no-repeat center;
  background-size: 70%;
}
.btn-dimensions {
  background: url('../icon/ruler-off.png') no-repeat center;
  background-size: 60%;
}
.dimensions {
  background: url('../icon/ruler-on.png') no-repeat center;
  background-size: 60%;
}