* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #333;
  overflow: hidden;
}

#container-loading {
  z-index: 2;
  background-color: #333;
  width: 100%;
  height: 100%;
  position: fixed;
  inset: 0;
}

.loading {
  box-sizing: border-box;
  border: 1px solid #186060;
  border-radius: 7px;
  width: 400px;
  height: 14px;
  margin-left: -200px;
  position: absolute;
  top: 48%;
  left: 50%;
}

#per {
  color: #0ff;
  background: #0ff;
  border-radius: 7px;
  width: 0;
  height: 100%;
  font-size: 14px;
  line-height: 13px;
}

#container-canvas {
  visibility: hidden;
}

.btn-controls {
  z-index: 1;
  cursor: pointer;
  background-color: #fffc;
  border-radius: 5px;
  width: 42px;
  position: fixed;
  bottom: 5%;
  right: 4%;
}

.btn-audio, .btn-run, .btn-dimensions {
  width: 42px;
  height: 42px;
}

.btn-audio {
  background: url("voice-off.c76cf8c1.png") center / 70% no-repeat;
}

.play {
  background: url("voice-on.f3d95c58.png") center / 70% no-repeat;
}

.btn-run {
  background: url("play-off.85d9061f.png") center / 70% no-repeat;
}

.run {
  background: url("play-on.ca4bc58a.png") center / 70% no-repeat;
}

.btn-dimensions {
  background: url("ruler-off.1fa12967.png") center / 60% no-repeat;
}

.dimensions {
  background: url("ruler-on.35baf0a1.png") center / 60% no-repeat;
}

